<template>
  <section :class="{ 'show-modal': show, 'hide-modal': !show }">
    <div class="modal-container">
      <div class="Cabecera">
        SEND NFT ðŸ”– {{ idtarget }}
        <button class="close" @click="closeModal">âœ–</button>
      </div>
      <div class="Contenido">
        <div class="loader-interno" v-if="loading"></div>
        <div class="group" v-if="!loading">
          <input type="text" v-model="wallet" required />
          <span class="highlight"></span>
          <span class="bar"></span>
          <label>Wallet destine</label>
        </div>
      </div>
      <div class="Bootonera">
        <span
          v-if="!loading"
          class="btn-sell-front text btn-connect"
          @click="confirmSend"
          >CONFIRM</span
        >
      </div>
    </div>
  </section>
</template>
<script>
import { toRefs, ref, watch, onUnmounted, onMounted } from "vue";
import useEncrypt from "../composables/useEncrypt";
const { encriptar, desencriptar } = useEncrypt();
import { mainStore } from "../store/mainStore";
import { useUserStore } from "../store/userStore";

import Swal from "sweetalert2";

export default {
  name: "ModalSendNFT",
  props: ["show", "idtarget"],
  emits: ["closeModalSend"],
  setup(props, { emit }) {
    const { show, idtarget } = toRefs(props);
    const loading = ref(false);
    const wallet = ref("");
    const store = mainStore();
    const userStore = useUserStore();

    onUnmounted(() => {
      wallet.value = "";
    });

    onMounted(() => {
      wallet.value = "";
    });

    const closeModal = () => {
      wallet.value = "";
      emit("closeModalSend", true);
    };

    const confirmSend = async () => {
      const w = wallet.value.toString().trim().replaceAll("\t", "");
      const Toast = Swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });

      if (!store.checkAddress(w)) {
        return Toast.fire({
          icon: "error",
          title: `Wallet is no valid`,
        });
      }

      loading.value = true;
      const id = idtarget.value;

      console.log("confirm & Send", w, id);
      let resp;

      try {
        resp = await store.transferNfts(w, id);
      } catch (error) {
        if (error.reason) {
          Toast.fire({
            icon: "error",
            title: error.reason,
          });
        }
        // (Note: canso de rechazo)
        loading.value = false;
        closeModal();
      }

      if (resp && resp.confirmations && resp.confirmations > 0) {
        await userStore.syncData();
        await store.searchNfts();
        console.log({ confirmations: resp.confirmations });
        store.searchWallet();

        Toast.fire({
          icon: "success",
          title: `Send success.`,
        });
        loading.value = false;
        closeModal();
      }

      console.log({ resp });
      loading.value = false;
      closeModal();
    };

    return {
      store,
      show,
      closeModal,
      confirmSend,
      loading,
      wallet,
    };
  },
};
</script>
