		return {
			conecto:false,
		};
	},
	setup() {
          const targetNFTid = ref(-1);
          const store = mainStore();
          const userStore = useUserStore();
          const updating = ref(false);
          const tmineSupply = ref(0);
          const nftSupply = ref(0);
          const networdId = ref(null);
          const showModalSend = ref(false);

          const vaultStoreInstance = vaultStore();

          const TmineAddressContract = CONTRACT_TMINE;

          const getDataNft = computed(() => {
               return userStore.getNfts;
          });
          const mostrarModalEnvio = () => {
               showModalSend.value = true;
               console.log('mostrarModalEnvio')
          };


          // store.searchSupplyTMINE().then((amount) => {
          //  tmineSupply.value = amount;
          // });

