               store.updateNFTsStakingBalances();
               console.log('updateFragments')
          };


          const addTToMetamask = async () => {

             const tokenSymbol = 'TMINE';
             const tokenDecimals = 18;
             const tokenImage = 'https://raw.githubusercontent.com/topaciotrade/topaciotrade/main/docs/_media/Tmine.svg';

             console.log('addTToMetamask')

             try {

               const configToken = {
                    type: 'ERC20',
                    options:{
                         symbol:'TMINE',
                         decimals:18,
                         image:tokenImage,
                         address:'0x9D44366dD501dc7010902Dd4293ba83E5da01301'
                    }
               };

               const wasAdded = await store.provider.send('wallet_watchAsset',configToken
                    );


                 if (wasAdded) {
                     console.log('Thanks for your interest!');
                 } else {
                     console.log('Your loss!');
                 }
             } catch (error) {
                 console.log(error);
             }
         }

          const getUrlIPFS = (meta)=>{
               //console.log({meta})
               return meta.image;
               
               const metaObj = JSON.parse(meta);
               if(!metaObj){ 
                    return `https://mir-s3-cdn-cf.behance.net/project_modules/max_1200/4aae0b77659967.5c8e7351a61f4.jpg`;
               }
               return metaObj.image;
          };

          const getNameFragment = (meta)=>{
               const metaObj = JSON.parse(meta);
               if(!metaObj){ 
                    return ``;
               }
               return metaObj.name;
          };

          const getAtributesFragment = (meta)=>{
               
               if(!meta||!meta.attributes){
                    return ``;
               }


               const cadena = meta.attributes.flatMap( (v,i) => `${v.trait_type}:${v.value}\n` ).toString().replaceAll(',','');

               return `${meta.type}\n${cadena}`;
          };

          const updateCacheNft = async(id)=>{
               updating.value = true;
               store.showLoader();
               await store.clearCacheAlchemy(id).then((r)=>{console.log({r},'***',id)})
               setTimeout(()=>{
                    updating.value = false;
                    store.hideLoader();
               },2000)
          };          

          const refresh = async()=>{
               console.log('refresh')
               updating.value = true;
               // await store.searchWallet();
               store.searchNfts();
               // await store.getHoldersCount();
               userStore.syncData();
               updating.value = false;
               console.log( 'updating...' , userStore.getNfts, store.getNFTs);
          };

          const targetNftToSend = (nft_token_id)=> {
               targetNFTid.value = parseInt(nft_token_id);
               showModalSend.value = true;
          };


          const toClaim = async (token_id) => {
               store.claimTmine(token_id).then(()=>{
                    userStore.syncData();
               }) 
          }
          

          store.getNetwork().then((chainId)=>{
