<template>
	<div class="loader-circular" v-if="!isLoad">
		<div></div>
		<div></div>
	</div>

	<img
		:src="getUrlIPFS(nft.metadata)"
		@load="onImgLoad"
		style="display: none"
	/>

	<div
		class="video anim"
		style="--delay: 0.4s"
		v-if="nft && nft.metadata && isLoad"
	>
		<div class="video-wrapper container-nft">
			<div
				class="img-nft"
				:style="`background-image:url(${getUrlIPFS(nft.metadata)})`"
			>
				<img
					src="/img/fly-svgrepo-com.svg"
					alt="send"
					class="icon-send"
					@click="targetNftToSend(nft.token_id)"
				/>
				<img
					src="/img/claim_.svg"
					alt="claim-img"
					class="icon-claim"
					@click="toClaim(nft.token_id)"
				/>

				<img
					src="/img/boveda.svg"
					@click="toVault(nft.token_id)"
					class="icon-to-vault"
					v-if="isOwnerVault"
				/>

				<svg
					viewBox="0 0 24 24"
					fill="none"
					stroke="currentColor"
					stroke-width="3"
					stroke-linecap="round"
					stroke-linejoin="round"
					class="feather feather-check"
				>
					<path d="M20 6L9 17l-5-5" />
				</svg>

				<div
					class="nft-title"
					v-if="!store.nfts.metadata"
					:data-tooltip="getAtributesFragment(nft.metadata)"
					data-position="top"
				>
					{{ nft.metadata.name }}
				</div>

				<!--Fragment #{{nft.token_id}} -->
				<label
					v-if="nft.reward"
					@click="updateFragments"
					class="lbl-staking"
				>
					<i class="coin-staking"
						><img src="/img/Tmine2.svg" alt="icon tmine"
					/></i>
					{{ formatReward(nft.reward) }} TMINE

					<div class="video-time">
						<a :href="nft.scan" target="_blank">
							<img
								src="/img/polygon-matic-logo.svg"
								alt="polygon-matic-logo"
								class="icon-blockchain"
							/>
						</a>
					</div>
				</label>
				<!-- <div class="nft-title" v-else>Topacio Gem #{{ nft.token_id }}</div> -->

				<!-- 				<img
					src="/img/update-meta.svg"
					@click="updateCacheNft(nft.token_id)"
					alt="update cache"
					class="icon-update-cache"
				/> -->
			</div>
		</div>
	</div>
</template>
<script>
import { mainStore } from "../store/mainStore";
import { useUserStore } from "../store/userStore";

import { vaultStore } from "../store/vaultStore";

import {
	ref,
	toRefs,
	computed,
	reactive,
	onMounted,
	onUnmounted,
	nextTick,
} from "vue";

export default {
	name: "Nft",
	props: ["nft"],
	emits: {
		changeTargetNFT: null,
	},

	setup(props, { emit }) {
		const vaultStoreInstance = vaultStore();
		const store = mainStore();
		const userStore = useUserStore();
		const { nft } = toRefs(props);
		const isLoad = ref(false);
		const reward = ref(0);

		const formatReward = (r) => {
			return parseFloat(r).toFixed(5)
		}
		const getUrlIPFS = (meta) => meta.image;

		const targetNftToSend = (nft_token_id) => {
			// emit to parrent to show modal
			emit("changeTargetNFT", nft_token_id);
		};

		const toClaim = async (token_id) => {
			await store.claimTmine(token_id).then(() => {
				userStore.syncData();
			});
		};

		const isOwnerVault = computed(() => {
			return (
				vaultStoreInstance &&
				vaultStoreInstance.getOwnerVault != null &&
				vaultStoreInstance.getWallet &&
				vaultStoreInstance.getOwnerVault ==
					String(vaultStoreInstance.getWallet).toUpperCase()
			);
		});

		async function toVault(token_id) {
			this.isLoad = false;
			const rDep = await vaultStoreInstance.toDepositInVault({
				nftNumber: token_id,
				price: "1000000000000000000000",
			});
			await rDep.wait(1);
			await userStore.syncData();
			console.log({ rDep, token_id });
			this.isLoad = true;
		}

		const getAtributesFragment = (meta) => {
			if (!meta || !meta.attributes) {
				return ``;
			}

			const cadena = meta.attributes
				.flatMap((v, i) => `${v.trait_type}:${v.value}\n`)
				.toString()
				.replaceAll(",", "");

			return `${meta.type}\n${cadena}`;
		};

		const updateCacheNft = async () => {};

		const onImgLoad = async () => {
			if (reward.value == 0) {
				reward.value = await store.updateRewardBalance(nft.value.token_id);
			}
			await nextTick();
			isLoad.value = true;
		};

		return {
			nft,
			getUrlIPFS,
			targetNftToSend,
			toClaim,
			getAtributesFragment,
			updateCacheNft,
			store,
			userStore,
			isLoad,
			onImgLoad,
			toVault,
			isOwnerVault,
			formatReward,
		};
	},
};
</script>

